@import url('https://fonts.googleapis.com/css?family=Borel');
@import url('https://fonts.googleapis.com/css?family=Kanit');
@import url('https://fonts.googleapis.com/css?family=Montserrat+Subrayada');


* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Kanit', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}
.list-container {
 border: 1px solid black;
 border-radius: 15px;
 padding: 10px;
 width: 425px;
 display: block;
 margin: 20px;
 background-color: rgb(255, 187, 204);
  box-shadow: 5px 3px 5px rgb(37, 48, 53);
}
.list-container:hover {
  transition-duration: 0.5s;
  scale: 110%;

}


.header {
  padding: 15px;
  font-size: 50px;
  font-family: 'Montserrat Subrayada', Arial, Helvetica, sans-serif;
  color:rgb(15, 24, 71);
  text-shadow: 4px 2px 2px rgb(68, 114, 126);
}
.header-span {
  font-size: 20px;
  margin: 20px;
  font-family: 'Borel', Arial, Helvetica, sans-serif;
  color:rgb(27, 16, 100);
  text-shadow: 4px 2px 2px rgb(109, 144, 158);
}
.nav-item-container {
  list-style: none;
  display: inline-block;
}

#nav-item {
  border: 1px solid black;
  padding: 10px;
  width: 200px;
  border-radius: 15px;
  margin: 20px;
  text-align: center;
  text-decoration: none;
  background-color: rgb(209, 236, 240);
  color: rgb(27, 16, 100);
  text-shadow: 2px 2px 2px rgb(131, 153, 168);
  box-shadow: 5px 3px 5px rgb(69, 106, 121);
}
.nav-item-container:hover {
  transition-duration: 0.5s;
  scale: 130%;

}

.sub-heading {
  border: 1px solid black;
  padding: 10px;
  width: 200px;
  border-radius: 15px;
  margin: 20px;
  text-align: center;
  font-family: 'Kanit', Arial, Helvetica, sans-serif;
  background-color: blanchedalmond;
  color: rgb(27, 16, 100);
  text-shadow: 4px 2px 2px rgb(206, 166, 115);
  box-shadow: 5px 5px 5px rgb(51, 73, 82);
}
.work-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.about-content-cont {
  border: 1px solid black;
  padding: 10px;
  width: 600px;
  border-radius: 15px;
  margin: 20px;
  text-indent: 50px ;
  background-color: rgb(255, 187, 204);
  box-shadow: 5px 3px 5px rgb(37, 48, 53);
}
.about-content {
  text-justify: none;
  font-size: 18px;  
  line-height: 30px;
  padding: 5px;
  font-family: 'Kanit', Arial, Helvetica, sans-serif;
  
}
.portfolio-app {
  background-image: url(./assets/banner0.jpg);
}

.skill-list {
  border: 1px solid black;
  padding: 10px;
  width: 600px;
  border-radius: 15px;
  margin: 20px;
  text-indent: 50px ;
  list-style: none;
  font-size: 20px;
  background-color: rgb(255, 187, 204);
  box-shadow: 5px 3px 5px rgb(37, 48, 53);
}
.resume-link {
  border: 1px solid black;
  padding: 10px;
  width: 600px;
  border-radius: 15px;
  margin: 20px;
  text-indent: 50px;
  font-size: 20px;
  background-color: rgb(255, 187, 204);
  box-shadow: 5px 3px 5px rgb(37, 48, 53);
}
.contact-form {
  border: 1px solid black;
  padding: 10px;
  width: 500px;
  border-radius: 15px;
  margin: 20px;
  font-size: 20px;
  background-color: rgb(255, 187, 204);
  box-shadow: 5px 3px 5px rgb(37, 48, 53);


}
.contact-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.contact-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.form-name {
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.form-email {
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.form-message {
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
#name-lbl, #name-txt {
  padding: 10px;
}
#email-lbl, #email-txt {
  padding: 10px;
}
#message-lbl, #message-txt {
  padding: 10px;
}
.form-submit {
  display: flex;
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  
}
.form-submit-btn {
  padding: 10px 150px;
  font-size: 20px;
  border-radius: 20px;
  background-color: rgb(191, 142, 197);
}
.form-submit-btn:hover {
  transition-duration: 0.5s;
  scale: 110%;
}

.footer {
  display: flex;
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.footer-github:hover, .footer-linkedin:hover, .footer-facebook:hover {
  transition-duration: 0.5s;
  scale: 120%;
}